
.fcard-main {
    display: flex;
}

#fcard {
    color: white;
    
}

.form-title2 {
   
    display: flex;
    justify-content: center;
    max-width: 550px;
    margin: 0 auto;
    /* background: rgb(220,220,220, 0.7);  */
    margin-bottom: 10px;
    border-radius: 5px;
    background-color: #f4511e; ;
    box-shadow: 0 0 10px rgba(0, 0, 0, 5);
    padding: 10px;
    text-align: center; 
    color: white;
    text-shadow: 0.2px 0.2px black;
    
}





.f-wrapper {
    position: absolute;
    top: 50%;
    transform: translateY(-47%);
    width: 100%;
    padding: 0 20px; 
  
}




.contact-form {
    max-width: 550px;
    margin: 0 auto;
    /* background: rgba(0, 0, 0, 0.7); */
    background: rgb(220,220,220, 0.2); 
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 20px;
    border-radius: 5px;
    display: flex;
    box-shadow: 0 0 10px rgba(0, 0, 0, 30);
    /* text-shadow: -1.0px 0 black;  */
    flex-direction: column;
    text-shadow: 0.2px 0.2px black;
    font-size: 15px;
    font-weight: bold;
    color: white;
    
    
}

.input-fields {
    display: flex;
    flex-direction: column;
    margin-right: 5%;
}

.input-fields, .msg {
    width: 48%;
}

.input-fields .input, .msg textarea {
    margin: 10px 0;
    /* background: transparent; */
    border: 0;
    /* border-bottom: 2px solid orangered; */
    outline: none;
    padding: 10px;
    width: 100%; 
    border-radius: 5px; 
    
}

.msg textarea {
    height: 70%;
}

::-webkit-input-placeholder {
    color: black;
}

::-moz-input-placeholder {
    color: black;
}

::-ms-input-placeholder {
    color: black;
}

