.f-btn {

    display: block;
    width: 80%;
    margin: auto;
    text-align: center;
    font-size: x-large;
    border: none;
    cursor: pointer;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    background-color: #f4511e; 
    color: white;
    padding: 16px 32px;
    font-size: 16px;
    opacity: 1;
    transition: 0.3s;
    margin-top: 37px;
    border-radius: 5px;
    cursor: pointer;
    text-transform: uppercase;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19); 
}

.f-btn:hover {
    opacity: 1;
    background-color: #b42a00; 
    /* background-color: black; */
    opacity: 1;
    /* outline: none; */
    /* outline: none; */
    /* border: 1px solid #e9c8a2; */
}

@media screen and (max-width: 600px) {
    .contact-form {
        flex-direction: column;
    }
    .form-title2 {
        flex-direction: column;
    }
    .msg textarea {
        height: 80px;
    }
    .input-fields,
    .msg {
        width: 100%;
    }
}

