.login-wrapper {
    position: relative;
    width: 100%;
    height: 100vh;
}



.login-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 500px;
    height: auto;
    padding: 40px;
    background: rgb(220,220,220, 0.8);
    font-size: 17px;
    /* background: white; */
    box-shadow: 0 0 10px rgba(0, 0, 0, 30);
    border-radius: 3%;
}

.login-container h2 {
    background-color: #f4511e; 
    text-align: center;
    padding: 16px 32px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    color: white;
    margin-bottom: 30px;
}


.login-container label,.login-container input{
    display: inline;
    margin: 5px;
    
}

.login-container .login-inp-container{
    display: flex;
    justify-content: space-between;
}

